<template>
  <div>
    <v-card style="border-radius: 40px;" color="#eaf1f6" class="my-4" :to="redireccionar ? { name: 'Alumnos' } : ''">
      <v-card-title v-if="bloqueaProductos" class="pa-2 text-center"
                    style="background-color: #EF5350;display: flex;margin-left: 85%;
                    border-bottom-left-radius: inherit">
        <v-btn icon :loading="loadingDesasignar" small dark  @click="$emit('desasignar-alumno', alumno)">
          <v-icon>mdi-close-thick</v-icon>
        </v-btn>
      </v-card-title>
      <v-card class="text-center d-flex flex-column justify-space-between align-center pa-4" color="transparent" flat>
        <v-avatar size="180px">
          <v-img v-if="alumno.refImagen" :src="alumno.refImagen" size="180px"/>
          <v-img v-else src="../../assets/nino.jpg" size="180px"/>
        </v-avatar>
      </v-card>

      <v-card-title class="text-center" style="word-break: normal!important; display: grid">
        <v-btn text large @click="bloqueaProductos ? $emit('editar-alumno', alumno) : ''">{{ alumno.nombreCompleto }}</v-btn>
      </v-card-title>
      <v-card-text class="mb-2 ma-4 mx-3">
        <v-row class="ma-2">
          <v-col cols="12" class="pb-0 pt-0">
            <span>{{ alumno.matricula }}</span>
          </v-col>
          <v-col cols="12" class="pb-0 pt-0">
            <span>{{ alumno.grado }}</span>
          </v-col>
          <v-col v-if="alumno.seccion" cols="12" class="pb-0 pt-0">
            <span>{{ alumno.seccion.titulo }}</span>
          </v-col>
          <v-col v-if="agregaSaldo" cols="12" class="pb-0 pt-0">
            <span class="font-weight-bold primary--text">{{ alumno.saldo | moneda  }}</span>
          </v-col>
          <v-col v-if="bloqueaProductos" cols="12" class="pb-0 pt-0">
            <span v-if="alumno.cafeteria">{{ alumno.cafeteria.nombre }}<v-btn icon x-small @click="dialogoAsignarCafeteria = true"><v-icon>mdi-lead-pencil</v-icon></v-btn></span>
            <span v-else>Sin cafetería default <v-btn icon x-small @click="dialogoAsignarCafeteria = true"><v-icon>mdi-lead-pencil</v-icon></v-btn></span>
          </v-col>
        </v-row>
      </v-card-text>

      <v-card-actions v-if="alumnoAgregado" class="font-weight-bold white--text"
                      style="display: flex;justify-content: center;align-items: center; background-color: #1867c0">
        <h4>Alumno Agregado</h4>
      </v-card-actions>

      <v-card-actions v-else-if="mostrarAcciones" style="display: flex;justify-content: center;align-items: center; background-color: #8bc34a">
        <v-btn v-if="bloqueaProductos" text dark @click="$emit('bloquearProductos')">Bloquear productos</v-btn>
        <v-btn v-if="agregaSaldo" text x-small dark @click="$emit('agregarSaldo')">Agregar saldo</v-btn>
        <v-btn v-if="muestraHistorial" text x-small dark @click="$emit('verHistorial')">Ver historial</v-btn>
        <v-btn v-if="seleccionar" text x-small dark @click="$emit('seleccionar')">Seleccionar</v-btn>
      </v-card-actions>
    </v-card>
    <dialogo v-model="dialogoAsignarCafeteria" title="Asignar cafeteria">
      <v-row>
        <v-col v-for="(cafeteria, indexCafeteria) in cafeterias" :key="indexCafeteria" cols="12" md="6">
          <v-card style="border-radius: 10px">
            <v-card-text>
              <v-row>
                <v-col cols="12">
                  <h3 class="primary--text">{{ cafeteria.nombre }}</h3>
                  <h4>{{ cafeteria.email }}</h4>
                  <h4>{{ cafeteria.telefono }}</h4>
                </v-col>
              </v-row>
            </v-card-text>
            <v-card-actions style="display: flex;justify-content: center;align-items: center; background-color: #8bc34a">
              <v-btn text small dark @click="seleccionarCafeteria(cafeteria)">Seleccionar</v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </dialogo>
  </div>
</template>

<script>
    // import Foto from "../General/Foto";
    import CategoriasService from "../../services/CategoriasService";
    import AlumnosService from "../../services/AlumnosService";
    import CafeteriasService from "../../services/CafeteriasService";
    import AuthConst from "../../config/AuthConst";

    export default {
        name: "Alumno",
      components: {},
      props: {
        alumno: {type: Object, required: true},
        bloqueaProductos: { type: Boolean, default: false },
        agregaSaldo: { type: Boolean, default: false },
        muestraHistorial: { type: Boolean, default: false },
        seleccionar: { type: Boolean, default: false },
        mostrarAcciones: { type: Boolean, default: false },
        redireccionar: {type: Boolean, required: false},
        loadingDesasignar: {type: Boolean, required: false},
        alumnoAgregado: {type: Boolean, required: false}
      },
      data(){
          return{
            dialogoAsignarCafeteria: false,
            cafeterias: []
          }
      },
      async created() {
        this.$loader = true
        try {
          this.cafeterias = await CafeteriasService.getCafeterias(this.$usuario.idCliente);
        } catch (e) {
        }
        this.$loader = false
      },
      methods: {
        async seleccionarCafeteria(cafeteria) {
          this.$loader = true;
          try {
            this.$set(this.alumno, 'cafeteria', cafeteria);
            let alumnoActualizado = await AlumnosService.updateAlumno(this.alumno);
            // Pendiente actualiza alumno con cafeteria directamente a $usuario
            const cafeteriaConSaldoAlumno = await CafeteriasService.getConSaldoAlumno(cafeteria.id, this.alumno.id);
            this.$set(this.alumno.cafeteria, 'saldoAlumno', cafeteriaConSaldoAlumno.saldoAlumno);
            localStorage.setItem(AuthConst.CURRENT_USER_KEY, JSON.stringify(this.$usuario));
            this.dialogoAsignarCafeteria = false
          } catch (e) {
            this.$showAlerta('600', 'error', 'mdi-alert-circle', 'No fue posible asignar cafeteria.', true);
            this.$router.go()
          }
          this.$loader = false;
        },

      }
    }
</script>

<style scoped>

