<template>
  <wrapper titulo="Alumnos">
    <v-row v-if="$usuario.hijos && $usuario.hijos.length > 0">
      <v-col v-for="(alumno, indexAlumno) in $usuario.hijos" :key="indexAlumno" cols="12" sm="6" md="4" lg="3">
        <alumno :alumno="alumno.alumno" mostrar-acciones :loading-desasignar="alumno.cargandoSaldo" bloquea-productos
                @bloquearProductos="abrirBloqueo(alumno)" @editar-alumno="abrirDialogOperarAlumno(alumno)"
                @desasignar-alumno="abrirDialogDesasignarAlumno(alumno)"/>
      </v-col>
      <v-btn fixed color="primary" bottom right fab @click="dialogBuscarAlumno = true">
        <v-icon>mdi-plus</v-icon>
      </v-btn>
    </v-row>

    <v-row v-else align="center" justify="center" style="display: flex;justify-content: center;align-items: center;">
      <v-col cols="12" class="text-center" style="display: flex;justify-content: center;align-items: center;">
        <img src="../../assets/niños_estado_vacio.svg" :style="$vuetify.breakpoint.smAndDown ? 'width:100%' : 'width:50%'"/>
      </v-col>
      <v-col cols="12" class="text-center">
        <h2 style="font-family: Nunito;" class="text--disabled">Aún no tiene alumnos agregados</h2>
      </v-col>
      <v-col cols="12" class="text-center">
        <v-btn rounded @click="dialogBuscarAlumno = true"><span style="font-family: Nunito;" class="primary--text">Agregar alumno</span></v-btn>
      </v-col>
    </v-row>

    <dialogo v-model="dialogCafeteriaDefault" title="Asignar cafetería default">
      <v-row>
        <v-col cols="12">
          <cafeteria/>
        </v-col>
      </v-row>
    </dialogo>

    <dialogo v-model="dialogBloquearProductos" :title="alumnoParaBloquearProducto.alumno ? 'Bloquear productos para ' + alumnoParaBloquearProducto.alumno.nombre : ''">
      <v-row>
        <v-col cols="12">
          <carrusel>
            <carrusel-item v-for="(categoria, indexCategoria) in categorias" :key="indexCategoria" size="60%" >
              <categoria :categoria="categoria"
                         :esta-seleccionada="categoriaSeleccionada && categoria.id === categoriaSeleccionada"
                         @seleccionarCategoria="seleccionarCategoria(categoria)"/>
            </carrusel-item>
          </carrusel>
        </v-col>
        <v-col v-for="(producto, IndexProducto) in productosFiltrados" :key="IndexProducto" cols="12" md="6">
          <producto opcion-bloquear :producto="producto" mostrar-acciones
                    :productos-bloqueados-alumno="alumnoParaBloquearProducto.alumno ? alumnoParaBloquearProducto.alumno.productosBloqueados : []"
                    @bloquear-producto="bloquearProducto(producto)"
                    @desbloquear-producto="desbloquearProducto(producto)" />
        </v-col>
      </v-row>
    </dialogo>

    <dialogo v-model="dialogBuscarAlumno" title="Buscar alumno">
      <template v-if="alumnosDisponibles.length <= 0">
        <v-form v-model="formNombre" ref="formNombre" lazy-validation v-click-outside="resetFormNombre">
          <v-row>
            <v-col cols="12" class="mt-3">
              <h3 class="text--disabled text-center">Buscar por nombre</h3>
              <v-text-field v-model="nombre" label="Nombre" :rules="nameRules" autofocus @keyup.enter="buscarAlumnoPorNombre" />
              <v-text-field v-model="apellidoPaterno" label="Apellido Paterno" :rules="nameRules"
                            @keyup.enter="buscarAlumnoPorNombre" />
              <v-text-field v-model="apellidoMaterno" label="Apellido Materno" :rules="nameRules"
                            @keyup.enter="buscarAlumnoPorNombre" />
            </v-col>
            <v-col cols="12" class="text-center">
              <v-btn :disabled="!nombre || !apellidoPaterno || !apellidoMaterno" rounded color="primary"
                     @click="buscarAlumnoPorNombre">Buscar</v-btn>
            </v-col>
          </v-row>
        </v-form>

        <v-row v-click-outside="resetFormMatricula">
          <v-col cols="12" class="mt-5">
            <h3 class="text--disabled text-center">Buscar por matrícula</h3>
            <v-text-field v-model="matricula" ref="matricula" label="Matrícula"
                          @keyup.enter="matricula ? buscarAlumnoPorMatricula() : ''" :rules="nameRules"/>
          </v-col>
          <v-col cols="12" class="text-center">
            <v-btn rounded color="primary" :disabled="!matricula" @click="buscarAlumnoPorMatricula">Buscar</v-btn>
          </v-col>
        </v-row>
      </template>
      <v-row v-else>
        <v-col v-for="(alumno, indexAlumnoDisponible) in alumnosDisponibles" :key="indexAlumnoDisponible" cols="12" md="6">
          <alumno :alumno="alumno" seleccionar mostrar-acciones :alumno-agregado="estaAlumnoSeleccionado(alumno)"
                  @seleccionar="seleccionarAlumno(alumno)" />
        </v-col>
      </v-row>
    </dialogo>

    <dialogo v-model="dialogOperarAlumno" :title="'Modificar '+ alumnoParaOperar.nombre">
      <v-row v-if="alumnoParaOperar.nombre">
        <v-col cols="12" sm="6">
          <v-text-field v-model="alumnoParaOperar.nombre" label="Nombre"/>
        </v-col>
        <v-col cols="12" sm="6">
          <v-text-field v-model="alumnoParaOperar.apellidoPaterno" label="Apellido Paterno"/>
        </v-col>
        <v-col cols="12" sm="6">
          <v-text-field v-model="alumnoParaOperar.apellidoMaterno" label="Apellido Materno"/>
        </v-col>
        <v-col cols="12" sm="6">
          <v-text-field v-model="alumnoParaOperar.matricula" label="Matrícula" disabled/>
        </v-col>
        <v-col cols="12" sm="6">
          <v-text-field v-model="alumnoParaOperar.grado" label="Grado" disabled/>
        </v-col>
        <v-col cols="12" sm="6">
          <v-select :value="alumnoParaOperar.seccion.titulo" :items="[alumnoParaOperar.seccion.titulo]" label="Sección" disabled/>
        </v-col>
        <v-col cols="12">
          <h3>Selecciona un icono para {{ alumnoParaOperar.nombre }}</h3>
        </v-col>
        <v-col cols="6" md="3" v-for="(imagen, indexImagen) in imagenes" :key="indexImagen">
          <v-card class="text-center v-btn" flat style="display: flex;justify-content: center;align-items: center;"
                  @click="seleccionarImagen(imagen.src)">
            <v-img :src="'https://images-hapit-lunch.s3.amazonaws.com/icons/' + imagen.src" width="70"/>
          </v-card>
        </v-col>
      </v-row>
      <template slot="actions">
        <v-btn text color="primary" plain @click="modificarAlumno">Guardar</v-btn>
      </template>
    </dialogo>

    <dialogo v-model="dialogDesasignarAlumno" :title="dialogDesasignarAlumno ? '¿Desea desasignar a ' + alumnoParaDesasignar.alumno.nombre + ' ' + alumnoParaDesasignar.alumno.apellidoPaterno + '?' : ''">
      <template slot="actions">
        <v-btn text color="primary" plain @click="desasignarAlumno">Aceptar</v-btn>
      </template>
    </dialogo>
  </wrapper>
</template>

<script>
    import Producto from "../Tienda/Producto";
    import Cafeteria from "./Cafeteria";
    import CategoriasService from "../../services/CategoriasService";
    import AlumnosService from "../../services/AlumnosService";
    import ClientesInternetService from "../../services/ClientesInternetService";
    import AuthConst from "../../config/AuthConst";
    import ProductosService from "../../services/ProductosService";
    import Carrusel from "../General/Carrusel/Carrusel";
    import CarruselItem from "../General/Carrusel/CarruselItem";
    import Categoria from "../Tienda/Categoria";
    import Alumno from "./Alumno";
    import CafeteriasService from "../../services/CafeteriasService";
    import OrdenesService from "../../services/OrdenesService";
    export default {
        name: "Alumnos",
      components: {Alumno, Categoria, CarruselItem, Carrusel, Cafeteria, Producto},
      data(){
          return{
            alumnoParaOperar: { base64: '' },
            dialogBuscarAlumno: false,
            dialogBloquearProductos: false,
            dialogOperarAlumno: false,
            buscarPorNombre: false,
            buscarPorMatricula: false,
            buscarPorQr: false,
            bloquearProductos: true,
            categorias: [
              { nombre: 'Bebidas', img: 'https://images.vexels.com/media/users/3/127674/isolated/preview/9fd717285f202af5437fd453cbe2e172-vaso-plano-de-bebidas.png' },
              { nombre: 'Dulces', img: 'https://cdn-icons-png.flaticon.com/512/194/194394.png' },
              { nombre: 'Frituras', img: 'https://creazilla-store.fra1.digitaloceanspaces.com/emojis/52747/french-fries-emoji-clipart-md.png' },
            ],
            dialogCafeteriaDefault: false,
            nombre: null,
            apellidoPaterno: null,
            apellidoMaterno: null,
            matricula: null,
            alumnosDisponibles: [],
            productos: [],
            alumnoParaBloquearProducto: {},
            dialogDesasignarAlumno: false,
            nameRules: [v => !!v || 'Este campo es requerido'],
            alumnoParaDesasignar: {},
            categoriaSeleccionada: null,
            productosFiltrados:[],
            formNombre: false,
            imagenes: [
              { src: 'automovil.png' },
              { src: 'balon.png' },
              { src: 'casco.png' },
              { src: 'corona.png' },
              { src: 'gato.png' },
              { src: 'leon.png' },
              { src: 'mandala.png' },
              { src: 'mariposa.png' },
              { src: 'ni%C3%B1o.png' },
              { src: 'ni%C3%B1a.png' }
            ]
          }
      },
      async created() {
        this.$loader = true
        try {
          this.categorias = await CategoriasService.getCategorias();
        } catch (e) {
        }
        this.$loader = false

        try {
          this.productos = await ProductosService.getProductos();
        } catch (e) {
          this.productos = []
        }

        try {
          this.ordenes = await OrdenesService.getOrdenesPorClienteAndEstatus(this.$usuario.id, 'CREADA')
        } catch (e) {
          this.ordenes = []
        }

        const vm = this
        for (const hijo of this.$usuario.hijos) {
          vm.$set(hijo, 'cargandoSaldo', true)
          CafeteriasService.getSaldoPorCafeteria(vm.$usuario.idCliente, hijo.alumno.id)
            .then(cafeterias => {
              vm.$set(hijo.alumno.cafeteria, 'saldoAlumno',
                vm.$getSaldoDeAlumnoParaCafeteria(hijo.alumno.cafeteria.id, cafeterias))
            })
            .catch(() => {})
            .finally(() => (vm.$set(hijo, 'cargandoSaldo', false)))
        }

        if(!this.categoriaSeleccionada){
          this.productosFiltrados = this.productos
        }

      },
      watch: {
        dialogBuscarAlumno(val) {
          if (val) {
            const vm = this;
            this.$nextTick(() => (vm.$refs.formNombre.reset()));
            this.matricula = null
            this.alumnosDisponibles = []
          }
        }
      },
      methods: {
        abrirBloqueo(alumno) {
          this.alumnoParaBloquearProducto = alumno
          this.dialogBloquearProductos = true
        },
        seleccionarImagen(path) {
          this.$set(this.alumnoParaOperar, 'refImagen', 'https://images-hapit-lunch.s3.amazonaws.com/icons/' + path)
        },
        async modificarAlumno() {
          this.$loader = true;
          try {
            let alumnoEditado = await AlumnosService.updateAlumno(this.alumnoParaOperar)
            const index = this.$usuario.hijos.findIndex(hijo => hijo.alumno.id === alumnoEditado.id)
            this.$set(this.$usuario.hijos[index], 'alumno', alumnoEditado)
            localStorage.setItem(AuthConst.CURRENT_USER_KEY, JSON.stringify(this.$usuario))
          } catch (e) {
            this.$showAlerta('600', 'red', 'mdi-alert-circle',
              'No fue posible modificar.', true);
          }
          this.$loader = false;
          this.dialogOperarAlumno = false
        },
        abrirDialogOperarAlumno(alumno) {
          this.alumnoParaOperar = JSON.parse(JSON.stringify(alumno.alumno))
          this.dialogOperarAlumno = true
        },
        estaAlumnoSeleccionado(alumno) {
          return this.$usuario.hijos.some(h => h.idAlumno === alumno.id)
        },
        async buscarAlumnoPorNombre() {
          if (this.$refs.formNombre.validate()) {
            this.$loader = true;
            try {
              this.alumnosDisponibles = await AlumnosService.busquedaPorNombre(this.nombre, this.apellidoPaterno, this.apellidoMaterno);
            } catch (e) {
              this.$showAlerta('600', 'error', 'mdi-alert-circle',
                'No se encontrarón alumnos.', true);
            }
            this.$loader = false;
          }
        },
        async buscarAlumnoPorMatricula() {
          this.$loader = true;
          try {
            this.alumnosDisponibles = [await AlumnosService.busquedaPorMatricula(this.matricula)]
          } catch (e) {
            this.$showAlerta('600', 'error', 'mdi-alert-circle',
              'No se encontrarón alumnos.', true);
          }
          this.$loader = false;
        },
        async seleccionarAlumno (alumno){
          this.$loader = true
          try {
            let alumnoParaAsignar = {
              id: 0,
              idClienteInternet: this.$usuario.id,
              idAlumno: alumno.id,
              alumno: alumno
            }
            let alumnoAsignado = await ClientesInternetService.createHijo(alumnoParaAsignar)
            this.$usuario.hijos.push(alumnoAsignado)
            localStorage.setItem(AuthConst.CURRENT_USER_KEY, JSON.stringify(this.$usuario))
            this.dialogBuscarAlumno = false
            this.$showAlerta('600', 'success', 'mdi-check-circle-outline', 'Alumno asignado.', true);
          } catch (e) {
            this.$showAlerta('600', 'error', 'mdi-alert-circle',
              'No se pudo asignar alumno, inténtelo más tarde.', true);
          }
          this.$loader = false
          this.dialogBuscarAlumno = false
        },

        bloquearProducto(producto){
          if (existeProductoEnOrden(this.ordenes, producto)) {
            this.$showAlerta('600', 'warning', 'mdi-alert-circle',
              'No es posible bloquear este producto porque se encuentra en una orden creada.', false);
          } else {
            let productoBloqueadoNuevo = { id: 0, producto, idAlumno: this.alumnoParaBloquearProducto.alumno.id }
            let vm = this
            vm.$loader = true
            ProductosService.bloquearProducto(productoBloqueadoNuevo).then(productoBloqueado => {
              vm.alumnoParaBloquearProducto.alumno.productosBloqueados.push(productoBloqueado)
              localStorage.setItem(AuthConst.CURRENT_USER_KEY, JSON.stringify(vm.$usuario))
            }).catch(error => {
              vm.$showAlerta('600', 'error', 'mdi-alert-circle',
                'No se pudo bloquear el producto, inténtelo más tarde.', true);
            }).finally(() => vm.$loader = false)
          }
        },
        desbloquearProducto(producto) {
          let vm = this
          const index = vm.alumnoParaBloquearProducto.alumno.productosBloqueados
            .findIndex(productoBloqueado => producto.id === productoBloqueado.producto.id)
          const productoBloqueadoNuevo = vm.alumnoParaBloquearProducto.alumno.productosBloqueados[index]
          vm.$loader = true
          ProductosService.desbloquearProducto(productoBloqueadoNuevo.id)
            .then(() => {
              vm.$delete(vm.alumnoParaBloquearProducto.alumno.productosBloqueados, index)
              localStorage.setItem(AuthConst.CURRENT_USER_KEY, JSON.stringify(vm.$usuario))
            })
            .catch(error => {
              vm.$showAlerta('600', 'error', 'mdi-alert-circle',
                'No se pudo desbloquear el producto, inténtelo más tarde.', true);
            }).finally(() => vm.$loader = false)
        },
        async abrirDialogDesasignarAlumno(hijo) {
          try {
            if (!hijo.alumno.cafeterias) {
              this.$loader = true
              this.$set(hijo.alumno, 'cafeterias', await CafeteriasService.getSaldoPorCafeteria(this.$usuario.idCliente, hijo.alumno.id))
            }
            const tieneSaldo = hijo.alumno.cafeterias.some(cafeteria => cafeteria.saldoAlumno)
            if (!tieneSaldo) {
              this.alumnoParaDesasignar = hijo
              this.dialogDesasignarAlumno = true
            } else {
              this.$showAlerta('600', 'warning', 'mdi-alert-circle',
                'No es posible desasignar el alumno si tiene saldo.', false);
            }
          } catch (e) {
            this.$showAlerta('600', 'warning', 'mdi-alert-circle',
              'No es posible desasignar el alumno si tiene saldo.', false);
          }
          this.$loader = false
        },
        desasignarAlumno(){
          let vm = this
          vm.$loader = true
          const index = vm.$usuario.hijos.findIndex(hijo => vm.alumnoParaDesasignar.id === hijo.id)
          AlumnosService.desasignarAlumno(vm.alumnoParaDesasignar.id)
            .then(() => {
              vm.$delete(vm.$usuario.hijos, index)
              localStorage.setItem(AuthConst.CURRENT_USER_KEY, JSON.stringify(vm.$usuario))
              vm.$showAlerta('600', 'success', 'mdi-check-circle-outline',
                'Alumno desasignado correctamente.', true);
            })
            .catch(error => {
              vm.$showAlerta('600', 'error', 'mdi-alert-circle',
                'No fue posible desasignar alumno.', true);
              console.log(error)
            })
            .finally(() => {
                vm.dialogDesasignarAlumno = false
                vm.$loader = false
            })
        },

        seleccionarCategoria(categoria) {
          let vm = this
          vm.categoriaSeleccionada = categoria.id
          if (vm.categoriaSeleccionada){
            vm.productosFiltrados = vm.productos.filter(function (productoFiltrado) {
              let idCategoriaProducto = productoFiltrado.categoria.id;
              return (idCategoriaProducto == vm.categoriaSeleccionada);
            })
          }else {
            vm.productosFiltrados = vm.productos
          }
        },
        resetFormNombre() {
          this.$refs.formNombre.resetValidation();
        },
        resetFormMatricula() {
          this.$refs.matricula.resetValidation();
        }
      }
    }

    function existeProductoEnOrden(ordenes, producto) {
      return ordenes.some(orden => orden.productos.some(p => p.producto.id === producto.id))
    }
</script>

<style scoped>

</style>
