import AuthConst from '../config/AuthConst';
import http from "./http";

const getByEmail = (email) => {
  let url = AuthConst.BASE_URL + '/clientes-internet/email/' + email;
  return http.get(url)
};

const getDataDatosFiscales = (id) => {
  let url = AuthConst.BASE_URL + '/datos-fiscales/datosfiscales/' + id;
  return http.get(url)
};



const createClienteInternet = (clienteInternet) => {
  let url = AuthConst.BASE_URL + '/clientes-internet';
  return http.post(url, clienteInternet)
};

const updateClienteInternet = (clienteInternet) => {
  let url = AuthConst.BASE_URL + '/clientes-internet';
  return http.put(url, clienteInternet)
};

const searchClienteInternet = (clienteInternet) => {
  return new Promise(async (resolve, reject) => {
    try {
      const url = AuthConst.BASE_URL + '/clientes-internet/email/' + clienteInternet.email;
      const clienteInternetEncontrado = await http.get(url);
      resolve(clienteInternetEncontrado)
    } catch (e) {
      const error = JSON.parse(JSON.stringify(e))
      if (error.status === 404) {
        try {
          const url = AuthConst.BASE_URL + '/clientes-internet';
          //
          //
          //
          //
          // Temporalmente asigna manualmente el idCliente 1
          const clienteInternetNuevo = await http.post(url, { ...clienteInternet, idCliente: 1, cliente: { id: 1 } });
          resolve(clienteInternetNuevo)
        } catch (e) {
          reject(e)
        }
      } else {
        reject(e)
      }
    }
  })
};

const createHijo = (hijo) => {
  let url = AuthConst.BASE_URL + '/clientes-internet/hijos';
  return http.post(url, hijo)
};

const getPdfData = (data)=>{
  console.log("data :",data)
  let url = AuthConst.BASE_URL + '/datos-fiscales/getPdfData';
  return http.multipart(url, data)
}


const saveTaxData = (data)=>{
  console.log("saveTaxData data :",data)
  let url = AuthConst.BASE_URL + '/datos-fiscales/saveTaxData';
  return http.post(url, data)
}

export default { getByEmail, createClienteInternet, updateClienteInternet, searchClienteInternet, createHijo, getDataDatosFiscales,getPdfData,saveTaxData}
